import React, {Component} from 'react';
//import ReactDOM from 'react-dom';
import './MobileBoxDisplay.css';


export class MobileBoxDisplay extends Component{
    render(){
        return ( 
            <div className = "mobileBox">
               <h3 className="titleTag">Open House 2021</h3>
                <p className="paraForMobileBox">Find out more about our exciting diploma programmes and life at NP in our virtual open house</p>
            <a href="https://join.np.edu.sg/" alt="NP Virtual Open House"><button className="mobileBoxButton">Learn More</button></a>
            </div>
        )
    }

}

export default MobileBoxDisplay;
