import React from 'react'
import { Helmet } from 'react-helmet';
import { Container, Row, Col } from 'react-bootstrap';
import '../../App.css';
import YouTube from 'react-youtube';
import soelogo from '../../images/soelogo.png';

function About() {
    const opts = {
      height: '400',
      width: '640',
      playerVars: {
        // https://developers.google.com/youtube/player_parameters
        modestbranding: 1,
        autoplay: 0,
      },
    };
    return (
      <>
        <Helmet>
          <title>About Us | Ngee Ann Polytechnic</title>
          <meta
            name="description"
            content="Ngee Ann Polytechnic SoE Building a better world through a broad-based multidisciplinary approach"
          />
        </Helmet>
        <Container fluid className="header-wrap">
          <Row className="about-header-banner-logo">
            <Container className="about-header-banner-text">
              <h1>About Us</h1>
            </Container>
          </Row>
          <Row className="about-us-body">
            <Col xs={12} md={12} className="tour">
              <h2>Virtual Tour</h2>
              <Container className="tour-container">
                <YouTube videoId="tRRAKXKFXXE" className="responsive-iframe" containerClassName="video" opts={opts} />
              </Container>
            </Col>
            <Col xs={12} md={12} className="summary">
              <Col xs={12} md={4} className="summary-logo">
                <img src={soelogo} alt="SoE Logo"/>
              </Col>
              <Col xs={12} md={8} className="summary-content">
                <h2>School of Engineering</h2>
                <p>
                  Building a better world through a broad-based, multidisciplinary approach at the most established engineering school.
                </p>
                <p>
                  Ngee Ann Polytechnic has been an established name in engineering education since 1967. Always keeping in times with the needs of the industry, the current unified School of Engineering (SoE) offers diverse fields in engineering and multiple learning pathways to cater to the needs of the students.
                </p>
                <p>
                  With its strong ties to the industry, SoE offers the latest technologies and great exposure to real-world learning, thus equipping our graduates with sought-after engineering skills and knowledge.
                </p>
                <p>
                  Our top-notch facilities, coupled with competent academic staff, promises an enriching education.
                </p>
              </Col>
            </Col>
            <Col xs={12} md={12} className="welcome">
              <Col xs={12} md={7} className="welcome-container">
                <h2>Welcome to School of Engineering!</h2>
                <p>
                  This is a Final Year Project that has been done by 3 of our aspiring Final Year students from their respective diploma programmes offered here in the School of Engineering.
                </p>
                <p>
                I am currently leading this team in development of this website. We hope that you have enjoyed our website and have learnt more about SoE.</p>
                <p>
                  We also warmly welcome you to join us in our virtual open house from 7 to 9 January 2021.
                </p>
              </Col>
              <Col xs={12} md={5} className="welcome-circle-container">
                <div className="circle koh-jun-xian-jaymeson-circle"></div>
                <h3>Koh Jun Xian Jaymeson</h3>
                <hr/>
                <h4>Project Lead</h4>
                <h6>Diploma in Aerospace Electronics with minor in Business Management</h6>
              </Col>
            </Col>
            <Col xs={12} md={12} className="team">
              <h2>Meet The Team</h2>
              <Row className="team-container">
                <Col xs={12} md={4} className="nakagawa-miho-sherry-container">
                  <Container className="team-circle">
                    <div className="circle nakagawa-miho-sherry-circle"></div>
                  </Container>
                  <Container className="team-content">
                    <h3>Nakagawa Miho Sherry</h3>
                    <hr/>
                    <h6>Diploma in Electronics and Computer Engineering</h6>
                    <p>Developer for Home page and World Skills</p>
                    <p>Passionate about all things related to Coffee and Code</p>
                  </Container>
                </Col>
                <Col xs={12} md={4} className="koh-jun-xian-jaymeson-container">
                  <Container className="team-circle">
                    <div className="circle koh-jun-xian-jaymeson-circle"></div>
                  </Container>
                  <Container className="team-content">
                    <h3>Koh Jun Xian Jaymeson</h3>
                    <hr/>
                    <h6>Diploma in Aerospace Electronics with Minor in Business Management</h6>
                    <p>Developer for Web Layout, Student Life, About Us</p>
                    <p>Loves fiddling with code in his free time and exploring the unknowns to pique his curiousity</p>
                  </Container>
                </Col>
                <Col xs={12} md={4} className="kugan-elamaran-container">
                  <Container className="team-circle">
                    <div className="circle kugan-elamaran-circle"></div>
                  </Container>
                  <Container className="team-content">
                    <h3>Kugan S/O Elamaran</h3>
                    <hr/>
                    <h6>Diploma in Electronics and Computer Engineering</h6>
                    <p>Developer for Courses, About Us</p>
                    <p>Passionate about football, music, and discovering code</p>
                  </Container>
                </Col>
                <Col xs={12} md={4} className="raymond-lam-sk-container">
                  <Container className="team-circle">
                    <div className="circle raymond-lam-sk-circle"></div>
                  </Container>
                  <Container className="team-content">
                    <h3>Raymond Lam S. K.</h3>
                    <hr/>
                    <h6>Senior Lecturer | School of Engineering</h6>
                    <p>Supervisor</p>
                  </Container>
                </Col>
              </Row>
            </Col>
          </Row>
        </Container>
      </>
    )
}

export default About
