import React, { Component } from 'react';
import './ContainerHomePage.css';

export class ContainerHomePage extends Component{
    render(){
        return(
            <div className="welcomeTitleDiv">
                <h3 className="welcomeTitle">Welcome to</h3>
                <h3 className="tagForNP">Ngee Ann Polytechnic</h3>
                <h3 className="tagForNP">School of Engineering</h3>
                <hr className="horizontalLineHomePage" />
                <p className="npTagLine1">Building a better world through a broad-based, multidisciplinary approach at the</p>
                <p className="npTagLine2">Most Established Engineering School</p>
            </div>
        )
    }
}

export default ContainerHomePage;
