import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import ScrollToTop from './components/pages/ScrollToTop';
import Navbar from './components/Navbar';
import Home from './components/pages/Home';
import About from './components/pages/About';
import Courses from './components/pages/Courses';
import StudentLife from './components/pages/StudentLife';
import WorldSkills from './components/pages/WorldSkills';
import Footer from './components/Footer';

function App() {
  return (
    <React.Fragment>
      <Router basename='np-school-of-engineering/'>
        <ScrollToTop />
        <Navbar />
        <Switch>
          <Route exact path='/' component={Home}/>
          <Route path='/about' component={About}/>
          <Route path='/courses' component={Courses}/>
          <Route path='/studentlife' component={StudentLife}/>
          <Route path='/worldskills' component={WorldSkills}/>
        </Switch>
        <Footer />
      </Router>
    </React.Fragment>
  );
}

export default App;