import React from 'react'
import { Helmet } from 'react-helmet';
import '../../App.css';

function WorldSkills() {
    return (
        <>
            <Helmet>
                <title>World Skills Singapore | Ngee Ann Polytechnic</title>
                <meta
                    name="description"
                    content="Ngee Ann Polytechnic SoE Building a better world through a broad-based multidisciplinary approach"
                />
            </Helmet>
            <div className="wip">
                <h1>Work-In-Progress</h1>
            </div>
        </>
    )
}

export default WorldSkills
